:root {
  --main-background: #84a178;
  --accent-color: #f8d24e;
  --dark-text-color: #131313;
  --light-text-color: #fcfcfc;
  --text-accent-color: #d6936d;
  --card-accent: #efd478;
  --hover-btn: #0a8817;
  --disabled: #ababab;
  --rights-reserved: #7a7a7a;
}

html {
  box-sizing: border-box;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

h1, h2, h3, h4, ul, p {
  margin: 0;
  padding: 0;
}

ul, a {
  text-decoration: none;
  list-style: none;
}

body {
  letter-spacing: .04em;
  color: var(--dark-text-color);
  margin: 0;
  font-family: Montserrat, sans-serif;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 1.75;
}

