.locations {
  background-color: var(--main-background);
}
.locations-list {
  display: flex;
  gap: 32px;
  padding-bottom: 64px;

  @media screen and (min-width: 768px) and (max-width: 1199px) {
    padding-bottom: 60px;
  }
}
.location-item {
  box-shadow: 0px 8px 30px rgba(212, 20, 67, 0.1);
  border-radius: 24px;
  background-color: #fcfcfc;
  width: 368px;
  padding: 40px;
  display: flex;
  flex-direction: column;
  gap: 32px;
  margin-top: -303px;
  font-weight: 500;
  @media screen and (min-width: 768px) and (max-width: 1199px) {
    padding: 40px 24px;
  }
}
.location-name {
  background-color: var(--accent-color);
  padding: 3px 9px;
  border-radius: 6px;
  width: fit-content;
}

.address {
  color: var(--text-accent-color);
  margin-top: 8px;

  @media screen and (min-width: 768px) and (max-width: 1199px) {
    font-size: 14px;
  }
}
.address:hover {
  text-decoration: underline;
}
.working-days {
  color: var(--dark-text-color);
  font-weight: 500;
  margin-bottom: 4px;
}
.working-hours {
  color: var(--disabled);
}
.open-hours {
  margin-bottom: 12px;
}
.dividing-line {
  border: 1px solid #e1e1e1;
  height: 0px;
  display: block;
}
.location-btn {
  display: flex;
  justify-content: center;
  padding-bottom: 120px;
  margin-bottom: -1px;
  @media screen and (min-width: 768px) and (max-width: 1199px) {
    padding-bottom: 40px;
  }
}
.location-btn button {
  font-weight: 600;
  border-radius: 10px;
  font-size: 14px;
  line-height: 1.28;
  cursor: pointer;
}
.btn-link {
  border-style: none;
  padding: 12px 34px;
  margin-right: 16px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: var(--accent-color);
  &:hover {
    background-color: var(--hover-btn);
  }
  &:focus {
    background-color: var(--hover-btn);
    color: var(--light-text-color);
    fill: var(--light-text-color);
  }
}

.franchise-btn {
  padding: 12px 46px;
  border: 2px solid var(--accent-color);
  background-color: var(--main-background);
  display: flex;
  justify-content: center;
  align-items: center;
  &:hover {
    border: 2px solid var(--hover-btn);
  }
  &:focus {
    border: 2px solid var(--hover-btn);
    color: var(--light-text-color);
    fill: var(--light-text-color);
  }
}
